$boxHeight: 350px;
$boxWidth: 260px;
$aniDur: 500ms ;

.App {
    height: 100vh;
    box-sizing: border-box;
    padding: 50px;
    text-align: center;
    background-color: #e8e8e8;
    display: flex;
    justify-content: center;
    font-family: Futura, "futura-pt", sans-serif;
}
.card-container {
    width: $boxWidth;
    height: $boxHeight;
    position: relative;
    cursor: pointer;

    &:hover {
        .card {
            width: $boxWidth * 1.1;
            height: $boxHeight * 1.1;
        }
        .code {
            animation: slideFade $aniDur ease-in-out;
            &._s {
                animation-delay: 0.1s;
            }
            &._t {
                animation-delay: 0.2s;
            }
        }
    }
}
.card {
    width: $boxWidth;
    height: $boxHeight;
    background-color: white;
    cursor: pointer;
    transition: all $aniDur ease-in-out;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.poster {
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 47%;
    width: 172px;
    height: 258px;
    transition: all 100ms linear;
    transform: rotate(5deg) translate(-50%, -50%);
}
.tips {
    color: #ec1752;
    font-size: 1.17em;
    transform: rotate(-90deg) translateX(-100%);
    transform-origin: center left;
    position: absolute;
    transition: all 200ms linear;
    left: 0;
    top: 0;
    z-index: 3;
    margin: 0;
    top: 0;
    cursor: pointer;
    font-weight: normal;
}

.code {
    color: #ec1752;
    font-size: 2.65em;
    position: absolute;
    z-index: 4;
    right: 0;
    bottom: 0;
    margin: 0;
    transform: translateX(100%);
    cursor: pointer;
    font-weight: bold;
    &._f {
        right: 2.65em * 0.6;
    }
    &._s {
        right: 2.65em * 0.3;
    }
}

.entranceAnimation {
    animation: slideFadeIn $aniDur ease-in-out;
    &._s {
        animation-delay: 0.1s;
    }
    &._t {
        animation-delay: 0.2s;
    }
}

@keyframes slideFade {
    0% {
        transform: translateX(100%, 0);
        opacity: 1;
    }
    50% {
        transform: translate(100%, -100%);
        opacity: 0;
    }
    51% {
        transform: translate(100%, 100%);
        opacity: 0;
    }
    100% {
        transform: translateX(100%);
        opacity: 1;
    }
}
@keyframes slideFadeIn {
    0% {
        transform: translateX(100%);
        opacity: 1;
    }
    50% {
        transform: translate(100%, 100%);
        opacity: 0;
    }
    51% {
        transform: translate(100%, -100%);
        opacity: 0;
    }
    100% {
        transform: translateX(100%);
        opacity: 1;
    }
}

.link {
    text-decoration: none;
    position: fixed;
    right: 10px;
    bottom: 10px;
    color: #666;
}